<template>
  <div>
    <a-alert
      message="เข้าห้องประชุม"
      description="โปรดกดปุ่มด้านล่าง เพื่อทำการสแกน QR code เพื่อเข้าห้องประชุม"
      type="info"
      show-icon
    />
 <a-spin :spinning="spinning">
      <div class="spin-content">

    <v-row class="" align="center" justify="center">
      <v-col class="mt-7" align="center">
        <v-img
          max-height="280px"
          max-width="280px"
          src="../../assets/img/test.png"
        >
          <v-img
            class="mt-8"
            v-show="this.state_qrcode == 0"
            max-height="210px"
            max-width="230px"
            src="../../assets/img/Qrcode-center.png"
          >
          </v-img>

          <div class="position_camera" v-if="this.state_qrcode == 1">
            <qrcode-drop-zone @decode="onDecode" @init="logErrors">
              <qrcode-stream
                @decode="onDecode"
                @detect="onDetect"
                @init="onInit"
              />
            </qrcode-drop-zone>

            <qrcode-capture v-if="noStreamApiSupport" @decode="onDecode" />
          </div>
        </v-img>
      </v-col>
    </v-row>
     </div>
    </a-spin>
    
    <v-row class="mt-5" align="center" justify="center">
      <v-col align="center">
        <a-button
          type="primary"
          icon="qrcode"
          size="large"
          style="height:80px;"
          @click="Open_Change_state_qrcode()"
        >
          กดเพื่อสแกนเข้าห้องประชุม
        </a-button>
      </v-col>
    </v-row>
 
  </div>
</template>

<script>
export default {
  components: {},

  data() {
    return {
      result: "",
      spinning: false,
      noStreamApiSupport: false,
      state_qrcode: 0,

    };
  },

  methods: {
    onDecode(result) {

      this.result = result;
      console.log(this.result);
      if (this.result != "") {
           this.spinning = !this.spinning;
 
      }
    },
    logErrors(promise) {
      promise.catch(console.error);
    },
    async onDetect(promise) {
      try {
        const {
          imageData, // raw image data of image/frame
        } = await promise;

        console.log(imageData);
      } catch (error) {
        console.log(error);
      }
    },
    Open_Change_state_qrcode() {
      console.log(this.state_qrcode);
      this.state_qrcode = 1;
   
    },
    async onInit(promise) {
      try {
        await promise;
      } catch (error) {
        if (error.name === "StreamApiNotSupportedError") {
          this.noStreamApiSupport = true;
        }
      }
    },
  },
};
</script>

<style scoped>
.error {
  font-weight: bold;
  color: red;
}
.position_camera {
  left: 0;
  right: 0;
  padding-top: 1.5em;
  padding-bottom: 1em;
  padding-left: 2em;
  padding-right: 2em;
  height: 275px;
  z-index: 100;
  display: flex;

  justify-content: center;
}
</style>
